<template>
    <div class="hello">
        <div id="container"></div>
        <div id="container2"></div>
    </div>
</template>

<script>
    import * as THREE from 'three';
    import {MTLLoader} from "three/examples/jsm/loaders/MTLLoader";
    import {OBJLoader} from "three/examples/jsm/loaders/OBJLoader";

    export default {
        name: 'HelloWorld',
        props: {
            msg: String
        },
        data() {
            return {
                camera1: null,
                scene1: null,
                renderer1: null,
                mesh1: null,
                camera2: null,
                scene2: null,
                renderer2: null,
                mesh2: null,
            };
        },
        methods: {
            init1() {
                let container1 = document.getElementById('container')
                this.camera1 = new THREE.PerspectiveCamera(45, container1.clientWidth / container1.clientHeight / 2, 0.1, 2000)
                this.camera1.position.x = -0.01
                this.camera1.position.z = 0.6

                this.scene1 = new THREE.Scene()
                const light = new THREE.AmbientLight('#fff', 5)
                // light.position.set(-100,-69,20)//光方向

                // let geometry = new THREE.BoxGeometry(0.2, 0.2, 0.2)
                // let material = new THREE.MeshNormalMaterial()
                // this.mesh = new THREE.Mesh(geometry, material)
                //
                // this.scene.add(this.mesh)

                let mtlLoader = new MTLLoader();
                mtlLoader.setPath('\\objmtl/');
                mtlLoader.load('20357_Cube_Bookcase_v1.mtl', (materials) => {
                    materials.preload();
                    // materials.color.set( 0xff0000 );

                    let objLoader = new OBJLoader();
                    objLoader.setMaterials(materials);
                    objLoader.setPath('\\objmtl/');
                    objLoader.load('20357_Cube_Bookcase_v1.obj', (obj) => {
                        obj.scale.x = 0.0075;
                        obj.scale.y = obj.scale.z = 0.015;
                        // obj.rotation.y = 0.2;
                        // obj.material.color.set( 0xff0000 );
                        obj.material = new THREE.MeshBasicMaterial({color: 0xFFFFFF});

                        this.mesh1 = obj;
                        this.mesh1.rotation.x = 1.45;
                        this.mesh1.rotation.y = 0;
                        this.mesh1.rotation.z = 0.3;
                        this.mesh1.position.z = this.mesh1.position.z/2;
                        this.scene1.add(this.mesh1);
                        // this.mesh.position.y = -90;
                        // let geometry = new THREE.BoxGeometry(0.2, 0.2, 0.2)
                        // let material = new THREE.MeshNormalMaterial()
                        // this.mesh = new THREE.Mesh(geometry, material)
                        // this.mesh.rotation.x = 0.5;
                        // this.mesh.rotation.x = 0.2;
                        // this.scene.add(this.mesh);
                        this.scene1.add(light);
                        this.renderer1 = new THREE.WebGLRenderer({antialias: true})
                        this.renderer1.setSize(container1.clientWidth, container1.clientHeight)
                        container1.appendChild(this.renderer1.domElement)
                        this.renderer1.render(this.scene1, this.camera1)
                    });
                }, () => {
                }, () => {
                    alert('error');
                })

            },
            init2() {
                let container2 = document.getElementById('container2')
                this.camera2 = new THREE.PerspectiveCamera(45, container2.clientWidth / container2.clientHeight / 2, 0.1, 2000)
                this.camera2.position.x = 0.01
                this.camera2.position.z = 0.6

                this.scene2 = new THREE.Scene()
                const light = new THREE.AmbientLight('#fff', 5)
                // light.position.set(-100,-69,20)//光方向

                let mtlLoader = new MTLLoader();
                mtlLoader.setPath('\\objmtl/');
                mtlLoader.load('20357_Cube_Bookcase_v1.mtl', (materials) => {
                    materials.preload();
                    // materials.color.set( 0xff0000 );

                    let objLoader = new OBJLoader();
                    objLoader.setMaterials(materials);
                    objLoader.setPath('\\objmtl/');
                    objLoader.load('20357_Cube_Bookcase_v1.obj', (obj) => {
                        obj.scale.x = 0.0075;
                        obj.scale.y = obj.scale.z = 0.015;
                        // obj.rotation.y = 0.2;
                        // obj.material.color.set( 0xff0000 );
                        obj.material = new THREE.MeshBasicMaterial({color: 0xFFFFFF});

                        this.mesh2 = obj;
                        this.mesh2.rotation.x = 1.45;
                        this.mesh2.rotation.y = 0;
                        this.mesh2.rotation.z = 0.3;
                        this.mesh2.position.z = this.mesh2.position.z/2;
                        this.scene2.add(this.mesh2);
                        // this.mesh.position.y = -90;
                        // let geometry = new THREE.BoxGeometry(0.2, 0.2, 0.2)
                        // let material = new THREE.MeshNormalMaterial()
                        // this.mesh = new THREE.Mesh(geometry, material)
                        // this.mesh.rotation.x = 0.5;
                        // this.mesh.rotation.x = 0.2;
                        // this.scene.add(this.mesh);
                        this.scene2.add(light);
                        this.renderer2 = new THREE.WebGLRenderer({antialias: true})
                        this.renderer2.setSize(container2.clientWidth, container2.clientHeight)
                        container2.appendChild(this.renderer2.domElement)
                        this.renderer2.render(this.scene2, this.camera2)
                    });
                }, () => {
                }, () => {
                    alert('error');
                })

            },
            animate1() {
                requestAnimationFrame(this.animate1)
                // this.mesh1.rotation.x += 0.01
                this.mesh1.rotation.z += 0.005
                this.renderer1.render(this.scene1, this.camera1)
            },
            animate2() {
                requestAnimationFrame(this.animate2)
                // this.mesh2.rotation.x += 0.01
                this.mesh2.rotation.z += 0.005
                this.renderer2.render(this.scene2, this.camera2)
            },
        },
        mounted() {
            this.init1();
            this.init2();
            setTimeout(() => {
                this.animate1();
                this.animate2();
            }, 1000);
        }
    }
</script>

